import { addFeatures } from "@esri/arcgis-rest-feature-layer";
import { request } from "@esri/arcgis-rest-request";

export const addSchoolFeatures = async ({
  rows,
  fields,
  userSession,
  featureLayerUrl,
}) => {
  const results = await addFeatures({
    authentication: userSession,
    url: featureLayerUrl,
    features: rows.map((row) => {
      let attributes = {};
      fields.forEach((field) => {
        attributes[field.name] = row[field.alias];
      });
      const feature = {
        attributes,
      };
      return feature;
    }),
  });

  return results;
};

// Helper method to quickly GET a resource
// https://esri.github.io/arcgis-rest-js/api/request/request/
export function get(url, params) {
  return new Promise((resolve, reject) => {
    request(url, { httpMethod: "GET", params: params })
      .then((response) => resolve(response))
      .catch((error) => reject(error));
  });
}
