import { useContext } from "react";
import DataLoaderContext from "../../context/DataLoaderContext";

import { Container } from "./styles";
import { CalciteP, CalciteH3 } from "calcite-react/Elements";
import CheckCircleIcon from "calcite-ui-icons-react/CheckCircleIcon";
import XCircleIcon from "calcite-ui-icons-react/XCircleIcon";

import SchoolList from "../SchoolList";

const Summary = ({ results }) => {
  const { config, data } = useContext(DataLoaderContext);
  const { addResults } = results;
  let errorSchools = [];
  addResults.forEach((item, idx) => {
    if (!item.success) {
      errorSchools.push(data[idx]);
    }
  });

  const errorMessage = errorSchools.length
    ? `There was an error submitting ${errorSchools.length} of ${addResults.length} schools. The below schools were not added.`
    : null;

  if (errorMessage) {
    return (
      <Container>
        <XCircleIcon size={100} color="red" style={{ margin: "auto" }} />
        <CalciteH3>Error</CalciteH3>
        <CalciteP>{errorMessage}</CalciteP>
        <SchoolList
          schools={errorSchools}
          schoolField={config.schoolNameFieldEsri}
        />
      </Container>
    );
  }

  return (
    <Container style={{ alignItems: "center" }}>
      <CheckCircleIcon size={100} color="green" style={{ margin: "auto" }} />
      <CalciteH3>{`Success! All ${addResults.length} schools updated.`}</CalciteH3>
    </Container>
  );
};

export default Summary;
