import { Container, Row, StyledList } from "./styles";

const SchoolList = ({ schools, schoolField }) => {
  const renderRow = ({ index, isScrolling, key, style }) => {
    const row = schools[index];

    return (
      <Row style={style} key={key} index={index}>
        {row[schoolField]}
      </Row>
    );
  };

  return (
    <Container>
      {schools.length ? (
        <StyledList
          height={Math.min(200, schools.length * 40)}
          overscanRowCount={10}
          rowCount={schools.length}
          rowHeight={40}
          rowRenderer={renderRow}
          width={600}
        />
      ) : (
        ""
      )}
    </Container>
  );
};

export default SchoolList;
