import React, { useEffect, useMemo, useState } from "react";
import { getLayer } from "@esri/arcgis-rest-feature-layer";
import { UserSession } from "@esri/arcgis-rest-auth";

import { addSchoolFeatures } from "../../helpers/helpers";

import Button from "calcite-react/Button";
import Loader from "calcite-react/Loader";
import Stepper, {
  Step,
  StepTitle,
  StepDescription,
} from "calcite-react/Stepper";

import Header from "../Header";
import FileUpload from "../FileUpload";
import {
  Container,
  ContentContainer,
  ContentFooter,
  ContentHeader,
} from "./styles";
import DataLoaderContext from "../../context/DataLoaderContext";
import Download from "../Download";
import Summary from "../Summary";

const AppSecured = ({ account, appConfig, attemptLogout }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [data, setData] = useState([]);
  const [fields, setFields] = useState([]);
  const [currentStep, setCurrentStep] = useState(1);
  const [results, setResults] = useState(null);

  const userSession = useMemo(() => new UserSession(account), [account]);

  useEffect(() => {
    const getFields = async () => {
      try {
        const layer = await getLayer({
          authentication: userSession,
          url: appConfig.featureServiceURL,
        });
        if (layer && layer.fields) {
          setFields(
            layer.fields.filter((field) => data[0].hasOwnProperty(field.alias))
          );
        }
      } catch (error) {
        console.error(error);
        setError("Error Saving Data. (Fields)");
      }
    };
    if (data.length) {
      getFields();
    }
  }, [data, userSession, appConfig]);

  const submitData = async () => {
    setLoading(true);
    const results = await addSchoolFeatures({
      rows: data,
      fields,
      userSession,
      featureLayerUrl: appConfig.featureServiceURL,
    });
    setResults(results);
    setCurrentStep(currentStep + 1);
    setLoading(false);
  };

  const nextLabels = ["Next", "Submit", "Restart"];

  return (
    <DataLoaderContext.Provider
      value={{
        loading,
        setLoading,
        error,
        setError,
        data,
        setData,
        config: appConfig,
      }}
    >
      <div>
        <Header account={account} attemptLogout={attemptLogout} />
        <Container>
          <ContentHeader>
            <Stepper currentStep={currentStep}>
              <Step>
                <StepTitle>Download</StepTitle>
                <StepDescription>
                  Download the Excel template to report school data.
                </StepDescription>
              </Step>
              <Step error={error && currentStep === 2}>
                <StepTitle>Upload</StepTitle>
                <StepDescription>
                  {error && currentStep === 2
                    ? error
                    : "Upload the excel template populated with your schools information."}
                </StepDescription>
              </Step>
              <Step>
                <StepTitle>Summary</StepTitle>
                <StepDescription>
                  Summary of your submitted school data.
                </StepDescription>
              </Step>
            </Stepper>
          </ContentHeader>
          <ContentContainer>
            {loading && (
              <Loader
                key="uploadLoader"
                sizeRatio={2}
                style={{ position: "absolute", top: "30%", left: "50%" }}
              />
            )}
            {currentStep === 1 && <Download fileUrl={appConfig.templateUrl} />}
            {currentStep === 2 && <FileUpload />}
            {currentStep === 3 && <Summary results={results} />}
          </ContentContainer>
          <ContentFooter>
            <div>
              {currentStep === 2 ? (
                <Button
                  clear
                  onClick={() => {
                    setCurrentStep(currentStep - 1);
                  }}
                  style={{ marginRight: ".5rem" }}
                >
                  Previous
                </Button>
              ) : (
                ""
              )}
              <Button
                disabled={currentStep === 2 && (!data || !data.length)}
                onClick={() => {
                  if (currentStep === 1) {
                    setCurrentStep(currentStep + 1);
                  } else if (currentStep === 2) {
                    submitData();
                  } else {
                    setData([]);
                    setCurrentStep(1);
                  }
                }}
              >
                {nextLabels[currentStep - 1]}
              </Button>
            </div>
          </ContentFooter>
        </Container>
      </div>
    </DataLoaderContext.Provider>
  );
};

export default AppSecured;
