import { useAccountManager } from "calcite-react/ArcgisAccount";
import { UserSession } from "@esri/arcgis-rest-auth";

import React, { useEffect, useState } from "react";
import { getLayer } from "@esri/arcgis-rest-feature-layer";

import AppSecured from "./AppSecured";
import Login from "../Login";

const App = ({ config }) => {
  const loginOptions = {
    ...config.authInfo,
    ...{ redirectUri: window.location.href },
  };
  const { accountManagerState, addAccount, logoutAccount } = useAccountManager(
    loginOptions
  );
  const { active, accounts } = accountManagerState;

  const [hasAccess, setHasAccess] = useState(null);

  useEffect(() => {
    const testAccess = async () => {
      try {
        const layer = await getLayer({
          authentication: new UserSession(accounts[active]),
          url: config.featureServiceURL,
        });
        if (layer && layer.fields) {
          setHasAccess(true);
        }
      } catch (error) {
        console.error(error);
        setHasAccess(false);
      }
    };
    if (active && accounts[active] && accounts[active].session) {
      testAccess();
    }
  }, [accounts, active, config.featureServiceURL]);

  const attemptLogin = () => {
    addAccount(loginOptions);
  };
  const attemptLogout = () => {
    logoutAccount(accounts[active]);
  };

  if (
    active &&
    accounts[active] &&
    accounts[active].session &&
    hasAccess !== null
  ) {
    if (hasAccess) {
      return (
        <AppSecured
          appConfig={config}
          account={accounts[active]}
          userSession={new UserSession(accounts[active].session)}
          attemptLogout={attemptLogout}
        />
      );
    } else {
      logoutAccount(accounts[active]);
      return <Login attemptLogin={attemptLogin} />;
    }
  } else {
    return <Login attemptLogin={attemptLogin} />;
  }
};

export default App;
